<template>
    <div v-if="bookdata.title">
        <span>
         {{bookdata.subject.title}}   
         </span>
    </div>
</template>
<script>
export default {
    data() {
        return {
            bookdata:''
        }
    },
    created() {
        this.goData(this.$route.params.id)
    },
    methods:{
        goData(id){
          let bhandle = `https://bird.ioliu.cn/v2?url=https://m.douban.com/rexxar/api/v2/market/book/` + id;
            axios.get(bhandle).then((res) => {
                console.log(res);
                this.bookdata = res.data.data
            })          
        }
    }
}
</script>